<template>
    <div class="box">
        <Head>
            <img src="../../images/110.png" alt="" slot="left" @click="go">
            <span slot="center">我问问</span>
        </Head>
        <div class="content">
            <div class="photo">
                <img src="../../images/304075_1180_664_70.jpg" alt="">
                <span>发111</span>
                <span>我问问</span>
                <span>评分4.8</span>
                <span>月售420单  售价￥20起</span>
                <span>评论数245  好评率85%</span>
            </div>
        </div>
    </div>
</template>

<script>
import Head from  '../Head/Header.vue'
import axios from 'axios'
export default {
    components:{
        Head
    },
    methods:{
        go(){
            this.$router.go(-1)
        }
    },
    mounted(){
        console.log(this.$route.params.item)
    }
}
</script>

<style lang="less" scoped>
   .box{
       width: 100%;
       header{
           width: 100%;
           position: relative;
           img{
               position: absolute;
               top: 0.2rem;
               left: 0.2rem;
           }
           span{
               display: block;
               position: absolute;
               width: 6rem;
               top: 0.3rem;
               left: 0.5rem;
               text-align: center;
               color: white;
               font-size: 0.5rem;
           }
       }
       .content{
           width: 100%;
         .photo{
             width: 100%;
              img{
                width: 100%;
                // height: 8rem;
              }
            span{
                display: block;
                margin-left: 0.2rem;
                margin-top: 0.1rem; 
                font-size: 0.2rem;
            }
         }
       }
   }
</style>


